@import '../vars.css';
.form {
  margin-left: 10rem;
  min-width: 23rem;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

input {
  width: 100%;
  height: 2.5rem;
  cursor: pointer;
  border-radius: 5px;
  border: 1px solid var(--color-grey-light);
  margin-top: 0.6rem;
  margin-bottom: 0.4rem;
  color: black;
  z-index: 100;
  font-size: 1.2rem;
}
input::placeholder {
  color: var(--color-grey-light);
  font-weight: 300;
}
input:focus {
  outline: none !important;
  border: solid 1px transparent;
  border-radius: 5px;
  background-image: linear-gradient(var(--color-white), var(--color-white)),
    var(--linear-gradient);
  background-origin: border-box;
  background-clip: padding-box, border-box;
}

label {
  width: 100%;
  justify-content: start;
  text-transform: uppercase;
  font-size: 0.8rem;
  font-weight: 500;
}

.form__cnt {
  display: flex;
  width: 100%;
  height: auto;
}

.form__cnt__small {
  width: 50%;
}

.inputs__months {
  display: flex;
}

.inputs__mon,
.inputs__mon:focus-visible {
  width: 30%;
  margin-right: 1rem;
}

.error {
  display: none;
}

input:invalid:focus + div.error {
  display: block;
  width: 100%;
  color: var(--color-red);
  margin-bottom: 1.4rem;
  font-size: 0.8rem;
}

.button {
  width: 100%;
  height: 3.2rem;
  background-color: var(--color-bg-dark);
  color: var(--color-white);
  font-size: 1rem;
  letter-spacing: 1px;
  border-radius: 5px;
}
.complete h2 {
  text-transform: uppercase;
  letter-spacing: 3px;
}
.complete p {
  margin-bottom: 2rem;
}

@media only screen and (max-width: 768px) {
  .form {
    margin: 0 auto;
    width: 22rem;
    height: auto;
    margin-top: 3rem;
  }
  label {
    font-size: 1.1rem;
  }
  .complete h2 {
    font-size: 2rem;
  }
  .complete p {
    font-size: 1.2rem;
  }
}
